
import XImg from 'vux/src/components/x-img/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default{
data(){
return {
userinfo: {
project_text: []
},
albums: [],
userid: this.$route.query.id,
category: '',
showcategory: false,
page: 1,
}
},
directives: {
TransferDom
},
components: {
Checker,
CheckerItem,
Group,
Cell,
Popup,
XImg
},
created(){
this.getuserinfo(this.userid);
this.getuserworks(this.userid, formatCategory(this.category, 0), this.page);
},
watch: {
'category'(){
this.getuserworks(this.userid, formatCategory(this.category, 0), this.page);
}
},
methods: {
setexp(exp){
var y = new Date().getFullYear();
if (exp >= y) {
return ""
} else if (exp < y) {
return (y - exp) + "";
} else {
return ""
}
},
getuserinfo(id){
var that = this;
ajax({
type: "get",
url: "http://houxiaopang.com/api/v1.1/resumeinfo",
data: {
'designer_id': id,
},
success(data){
if (data.code === 0) {
that.userinfo = data.data;
} else {
alert("");
}
},
error(){
alert("");
}
});

},
getuserworks(id, c, page){
var that = this;
ajax({
type: "get",
url: "http://houxiaopang.com/api/v1.1/allwork",
data: {
'category': c,
'designer_id': id,
'page': page
},
success(data){
if (data.code == 0) {
that.albums = [];
var json = data.data.album;
for (var i = 0, size = json.length; i < size; i++) {
json[i].category = formatCategory(json[i].category, 1);
that.albums.push(json[i]);
}
} else {
alert("");
}
},
error(){
alert("");
}
});
},
formatWorktime(value){
var worktime = "";
if (value == "1") {
worktime = "";
} else if (value == "2") {
worktime = "";
} else if (value == "3") {
worktime = "";
} else if (value == "4") {
worktime = "";
} else if (value == "5") {
worktime = "";
}
return worktime;
},
oncatechick(){
this.showcategory = true;
},
onItemClick () {
if (!this.disabled) {
this.showcategory = false
}
},
showmore(){
this.more = !this.more;
if (this.more) {
document.getElementById("project_text").style.height = "auto";
document.querySelector(".userinfo .more i").innerHTML = "&#xe8fa;";
document.querySelector(".userinfo .more span").innerHTML = "";
} else {
document.getElementById("project_text").style.height = "60px";
document.querySelector(".userinfo .more i").innerHTML = "&#xe8f8;";
document.querySelector(".userinfo .more span").innerHTML = "";
window.scrollTo(0, 0);
}
},
}
}



