
import XImg from 'vux/src/components/x-img/index.vue'

export default {
components: {
XImg
},
data () {
return {
projectinfo: {},
userinfo: {},
album_id: this.$route.query.album_id,
user_id: this.$route.query.user_id
}
},
created(){
this.getuserinfo(this.user_id);
this.getAlbumInfo(this.album_id);
},
methods: {
setexp(exp){
var y = new Date().getFullYear();
if (exp >= y) {
return ""
} else if (exp < y) {
return (y - exp) + "";
} else {
return ""
}
},
getAlbumInfo(id){
var that = this;
ajax({
type: "get",
url: "http://houxiaopang.com/api/v1.1/albumdetail",
data: {'album_id': id},
success(result){
if (result.code === 0) {
that.projectinfo = result.data;
that.projectinfo.category = formatCategory(that.projectinfo.category, 1);
} else {
location.href = "http://houxiaopang.com/404";
}
}
});
},
getuserinfo(id){
var that = this;
ajax({
type: "get",
url: "http://houxiaopang.com/api/v1.1/resumeinfo",
data: {
'designer_id': id,
},
success(data){
if (data.code === 0) {
that.userinfo = data.data;
} else {
alert("");
}
},
error(){
alert("");
}
});
},
}
}



